<template>
<div>
	<div id="userBtn" 
		v-on:click="userK">
		
	</div>
	<div id="userBox"
		v-show="userKG"
		v-on:click="userK"
		>
		<div class="userB"
			v-on:click.stop>
			<div>
				
			</div>
			<div></div>
		</div>
	</div>
</div>
</template>

<script>
export default {
	name:'user',
	data:function() {
		return {
			userKG:false,
		}
	},
	methods:{
		userK:function() {
			this.userKG = !this.userKG
		}
	}
}
</script>
<style scoped>
#userBtn {
  height: 60px;
  width: 60px;
  background-image: url(../../../assets/img/userBtn.png);
  background-repeat: no-repeat;
  background-size: 40px 40px;
  background-position: center;
}
#userBox {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 12;
  background: rgba(0, 0, 0, 0.4);
}
#userBox .userB {
  height: 100%;
  width: 50%;
  background: #ffffff;
}
</style>


